import { Grid, Box } from '@mui/material'
import * as React from 'react'
import { CardFrom } from './components/CardFrom'
import { CardTo } from './components/CardTo'
import { AppContext, AppContextType } from './contexts'

export function TablePage() {
  const { store, dispatch } = React.useContext(AppContext) as AppContextType
  const [selectedFrom, setSelectedFrom] = React.useState<string | number>('')
  const [selectedTo, setSelectedTo] = React.useState<(string | number)[]>([])
  if (store.tableState.list.length === 0) {
    dispatch({
      type: 'PAGE_CHANGE',
      event: { key: 'pageIndex', value: 0 }
    })
  }
  const ref = React.useRef<HTMLDivElement>(null)

  const onClickFrom = (id: string | number) => {
    setSelectedFrom(id)
    setSelectedTo([])
  }
  const onClickTo = (id: string | number) => {
    if (id === selectedFrom) {
      setSelectedFrom('')
      setSelectedTo([])
      dispatch({ type: 'MATCHED', event: { key: 'id', value: id } })
    } else if (selectedFrom !== '' && !selectedTo.includes(id)) {
      dispatch({ type: 'MISMATCHED', event: { key: 'id', value: id } })
      setSelectedTo([...selectedTo, id])
    }
  }
  return (
    <Box sx={{ pb: 7 }} ref={ref}>
      <Grid
        container
        sx={{
          top: '4.5em',
          width: '100%',
          position: 'absolute',
          bottom: '4em',
          left: 0,
          textAlign: 'center'
        }}
      >
        <Grid item xs={6} sx={{ borderRight: 1, textAlign: 'center' }}>
          {store.tableState.visible.from.map((item) => (
            <CardFrom
              key={item}
              id={item}
              selected={selectedFrom === item}
              onClick={onClickFrom}
            />
          ))}
        </Grid>

        <Grid
          item
          xs={6}
          sx={{
            borderLeft: 1,
            textAlign: 'center'
          }}
        >
          {store.tableState.visible.to.map((item) => (
            <CardTo
              key={item}
              id={item}
              selected={selectedTo.includes(item)}
              onClick={onClickTo}
            />
          ))}
        </Grid>
      </Grid>
    </Box>
  )
}
