import HomeIcon from '@mui/icons-material/Home'
import VocabularyIcon from '@mui/icons-material/LibraryBooksOutlined'
import PersonOutlinedIcon from '@mui/icons-material/PersonOutlined'
import { BottomNavigation, BottomNavigationAction, Paper } from '@mui/material/'
import * as React from 'react'
import { AppContext, AppContextType } from '../contexts'

export function Footer() {
  const { store, dispatch } = React.useContext(AppContext) as AppContextType
  return (
    <Paper
      sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }}
      elevation={3}
    >
      <BottomNavigation
        showLabels
        value={store.pageIndex}
        onChange={(_event, pageIndex) => {
          dispatch({
            type: 'PAGE_CHANGE',
            event: { key: 'pageIndex', value: pageIndex }
          })
        }}
      >
        <BottomNavigationAction icon={<HomeIcon />} />
        <BottomNavigationAction disabled={store.tableState.list.length === 0} icon={<VocabularyIcon />} />
        <BottomNavigationAction icon={<PersonOutlinedIcon />} />
      </BottomNavigation>
    </Paper>
  )
}
