import { Paper, Zoom } from '@mui/material'
import * as React from 'react'
import { DragDropContainer } from 'react-drag-drop-container'
import { AppContext, AppContextType } from '../contexts'
import { getTheme } from '../engine/theme'
import { IItemProps, IDeckItem } from '../interfaces'
import { seedItem } from '../engine'

export const CardFrom: React.FC<IItemProps> = ({ id, onClick, selected }) => {
  const { store } = React.useContext(AppContext) as AppContextType

  const currentTheme = getTheme(store.themeIndex)
  const deckItem = store.tableState.deck?.Items.find(
    (item: IDeckItem) => item.id === id
  )
  let innerHTML = <b>!ERROR:{id}</b>
  if (deckItem !== undefined) {
    innerHTML = <span>{seedItem(deckItem.from)}</span>
  }

  // onDrop = {() => {console.log('on drop')}} onDragStart = {() => {console.log('on onDragStart')}} onDrag = {() => {console.log('on onDrag')}}  onDragEnd  = {() => {console.log('on onDragEnd')}}
  return (
    <DragDropContainer
      targetKey="hansom"
      dragData={id}
      render={() => {
        return (
          <Zoom in={true} style={{ transitionDelay: '500ms' }}>
            <Paper
              onClick={() => onClick(id)}
              sx={{
                cursor: 'grabbing',
                fontSize: store.fontSize,
                position: 'relative',
                textAlign: 'center',
                color: currentTheme.palette.text.primary,
                borderRadius: '2em',
                borderStyle: 'solid',
                padding: '1em',
                width: '40vw',
                display: 'block',
                marginLeft: 'auto',
                marginRight: 'auto',
                marginTop: '1em',
                userSelect: 'none',
                transition: 'background-color .1s ease-in',
                backgroundColor: selected
                  ? currentTheme.palette.background.default
                  : currentTheme.palette.background.paper
              }}
            >
              {innerHTML}
            </Paper>
          </Zoom>
        )
      }}
    />
  )
}

export default CardFrom
