import { Fade, Paper, Zoom } from '@mui/material'
import * as React from 'react'
import { AppContext, AppContextType } from '../contexts'
import { getTheme } from '../engine/theme'
import { IItemProps, IDeckItem } from '../interfaces'
import { seedItem } from '../engine'
import { DropTarget } from 'react-drag-drop-container'

export const CardTo: React.FC<IItemProps> = ({ id, onClick, selected }) => {
  const { store, dispatch } = React.useContext(AppContext) as AppContextType
  const [isHover, setHover] = React.useState(false)
  const currentTheme = getTheme(store.themeIndex)
  const deckItem = store.tableState.deck?.Items.find(
    (item: IDeckItem) => item.id === id
  )
  let innerHTML = <b>!ERROR:{id}</b>
  if (deckItem !== undefined) {
    innerHTML = <span>{seedItem(deckItem.to)}</span>
  }

  return (
    <DropTarget
      targetKey="hansom"
      dropData={id}
      onHit={(e: any) => {
        if (e['dragData'] === id) dispatch({ type: 'MATCHED', event: { key: 'id', value: id } })
        setHover(false)
      }}
      onDragEnter={() => setHover(true)}
      onDragLeave={() => setHover(false)}
      render={() => {
        return (
          <Zoom in={true} style={{ transitionDelay: '500ms' }}>
            <Paper
              onClick={() => onClick(id)}
              sx={{
                position: 'relative',
                fontSize: store.fontSize,
                textAlign: 'center',
                color: currentTheme.palette.text.primary,
                borderRadius: '2em',
                padding: '1em',
                width: '40vw',
                display: 'block',
                marginLeft: 'auto',
                borderStyle: 'solid',
                marginRight: 'auto',
                marginTop: '1em',
                transition: 'background-color .1s ease-in',
                backgroundColor:
                  selected || isHover
                    ? currentTheme.palette.background.default
                    : currentTheme.palette.background.paper
              }}
            >
              {innerHTML}
            </Paper>
          </Zoom>
        )
      }}
    />
  )
}

export default CardTo

