import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import {
  Avatar,
  Divider,
  List,
  ListItem,
  ListItemButton,
  ListItemText
} from '@mui/material'
import Accordion from '@mui/material/Accordion'
import AccordionDetails from '@mui/material/AccordionDetails'
import AccordionSummary from '@mui/material/AccordionSummary'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import axios from 'axios'
import * as React from 'react'
import { AppContext, AppContextType } from './contexts'
import { IDeckList } from './interfaces'
import { getTheme } from './engine/theme'

export function HomePage() {
  const { store, dispatch } = React.useContext(AppContext) as AppContextType  
  const ref = React.useRef<HTMLDivElement>(null)
  const currentTheme = getTheme(store.themeIndex)

  React.useEffect(() => {
    axios
      .get('/json/index.json')
      .then( response => dispatch({
        type: 'LOAD_DECK_LIST',
        event: {
          key: 'deckList',
          value: response.data
        }}))
      .catch(error =>
        dispatch({
          type: 'ERROR',
          event: {
            key: 'error',
            value: error
          }
        })
      )
  }, [])

  const handleChange =
    (panel: string) => (_event: React.SyntheticEvent, isExpanded: boolean) => {
      dispatch({
        type: 'EXPAND_PANEL',
        event: {
          key: 'expandedPanel',
          value: isExpanded ? panel : ''
        }
      })
    }

  const fncOnClick = (category: string) => {  
    dispatch({
      type: 'LOAD_CATEGORY',
      event: {
        key: 'category',
        value: category
      }
    })
  }

  return (
    <Box sx={{ pb: 7, width: '100%' }} ref={ref} alignItems={'center'}>
      <Accordion
        expanded={store.expandedPanel === 'first'}
        onChange={handleChange('first')}
      >
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography variant="overline">
            What is it and How It Works?
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography variant="body1">
            The app helps you memorize associations effectively by using spaced
            repetition and adaptive algorithms. It tracks your progress and
            performance, and adjusts the frequency and difficulty of the{' '}
            <b>flash cards</b>
            accordingly. It also gives you feedback and tips on how to improve
            your memory. What are flash cards?
          </Typography>
          <Typography variant="body1">
            <ul>
              <li>
                Flash cards are a popular and effective way to memorize
                associations like words on different languages.
              </li>
              <li>
                Flash cards are small cards with a word or image on one side and
                the associated word or image on the other side.
              </li>
              <li>
                Flash cards help learners to associate the words with their
                meanings, imagination, and usage.
              </li>
              <li>
                Flash cards can be used in various ways, such as reviewing,
                testing, sorting, or playing games.
              </li>
              <li>
                Flash cards can be made by hand, printed, or accessed online or
                through apps.
              </li>
            </ul>
          </Typography>
          <Typography variant="body1">
            All flash cards are joined into one deck. Any decks is assigned to
            the specific category. Pick your one from list to start and see how
            you can improve your memory and fill the lack in your knowledge.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={store.expandedPanel === 'second'}
        onChange={handleChange('second')}
      >
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography variant="overline">
            Available Decks by Category
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <List>
            {store.deckList &&
              Object.keys(store.deckList).map((key) => (
                <ListItem key={key} disablePadding>
                  <ListItemButton onClick={() => fncOnClick(key)}>
                    <Avatar
                      sx={{
                        marginRight: 2,
                        fontSize: store.fontSize,
                        color: currentTheme.palette.background.paper,
                        backgroundColor: currentTheme.palette.text.primary
                      }}
                      aria-label="recipe"
                    >
                      {key.toUpperCase()[0]}
                    </Avatar>
                    <ListItemText primary={key} />
                  </ListItemButton>
                </ListItem>
              ))}
          </List>
        </AccordionDetails>
      </Accordion>
    </Box>
  )
}
