import { ITableStateType } from '../contexts'
import { IDeck, IDeckItem, ItemType } from '../interfaces'

export function shuffle<T> (o: T[]): T[] {
  for (let j, x, i = o.length; i; j = Math.floor(Math.random() * i), x = o[--i], o[i] = o[j], o[j] = x);
  return o
}

export function seedNumber<T> (arr: T[]): number {
  return Math.floor(Math.random() * arr.length)
}

export function seedItem<T> (arr: T[] | T): T {
  return Array.isArray(arr) ? arr[seedNumber(arr)] : arr
}

export function loadDeck (Decks:IDeck[], DeckID: string | null | undefined): IDeck {
  const deck = Decks.find(item => item.DeckID === DeckID)
  if (!deck) throw new Error('Wrong Deck Id')
  const result: IDeck = {
    Title: deck.Title,
    DeckID: deck.DeckID,
    Description: deck.Description,
    FromType: deck.FromType,
    ToType: deck.ToType,
    Items: deck.Items.map(item => {
      return {
        ...item,
        matches: 0
      }
    })
  }
  return result
}

export function shuffleCards (deck: IDeck | undefined, cntFrom: number, cntTo: number): ITableStateType {
  if (!deck) return {
    visible: { from: [], to: [] },
    list: []
  }
  const listFrom = [...shuffle(deck.Items)]
  const listTo: IDeckItem[] = []

  const visible = {
    from: [] as number[],
    to: [] as number[]
  }

  for (let i = 0; i < cntFrom; i++) {
    const item = listFrom.shift()
    if (item) listTo.push(item)
    visible.from.push(item?.id ?? 0)
  }

  for (let j = cntFrom; j < cntTo; j++) {
    visible.to.push(listFrom.shift()?.id ?? 0)
  }

  visible.to = shuffle([...visible.from, ...visible.to])
  return {
    deck,
    visible,
    list: [...listFrom, ...listTo]
  }
}

export function dealCards (decks: IDeck[], deckId: string, cntFrom: number, cntTo: number): ITableStateType {
  const deck = loadDeck(decks, deckId)
  return shuffleCards(deck, cntFrom, cntTo)
}

export function replaceCards (id: string | number, tableState: ITableStateType): ITableStateType {
  const result: ITableStateType = {
    ...tableState
  }
  result.visible.from = result.visible.from.filter(item => item !== id)
  result.visible.to = result.visible.to.filter(item => item !== id)
  const matchedElement = result.list.find(item => item.id === id)
  if (matchedElement) matchedElement['matches'] = (matchedElement.matches ?? 0) + 1
  const minMatch = Math.min.apply(
    null, 
    result.list.map(item => item.matches))
  let minList = result.list.filter(item => item.matches === minMatch && !result.visible.to.includes(item.id) && !result.visible.from.includes(item.id))
  if (minList.length === 0) minList = result.list.filter(item => !result.visible.to.includes(item.id) && !result.visible.from.includes(item.id))
  const newTo = seedItem(minList).id
  result.visible.to.push(newTo)
  result.visible.from.push(seedItem(result.visible.to.filter(item => !result.visible.from.includes(item))))
  return result
}

export function mismatchCards (id: string | number, tableState: ITableStateType): ITableStateType {
  const result: ITableStateType = {
    ...tableState
  }
  const matchedElement = result.list.find(item => item.id === id)
  if (matchedElement) {
    matchedElement['matches'] = (matchedElement.matches ?? 0) - 1
    if (matchedElement.matches < 0) matchedElement.matches = -1
  }
  return result
}