import { createTheme, Color, colors, Theme } from '@mui/material/'

export const availableColors: Color[] = [
  colors.blue,
  colors.blueGrey,
  colors.brown,
  colors.cyan,
  colors.deepOrange,
  colors.deepPurple,
  colors.green,
  colors.grey,
  colors.indigo,
  colors.lightBlue,
  colors.lightGreen,
  colors.lime,
  colors.orange,
  colors.pink,
  colors.purple,
  colors.red,
  colors.teal,
  colors.yellow
]

export function getTheme(colorIndex: number): Theme {
  return createTheme({
    typography: {
      fontFamily: 'Verdana',
    },
    palette: {
      background: {
        paper: availableColors[colorIndex][900],
        default: availableColors[colorIndex][300]
      },
      mode: 'dark',
      primary: {
        main: availableColors[colorIndex][50]
      },
      secondary: {
        main: availableColors[colorIndex][500]
      },
      warning: {
        main: availableColors[colorIndex][500]
      },
      info: {
        main: availableColors[colorIndex][500]
      },
      success: {
        main: availableColors[colorIndex][50]
      },
      error: {
        main: colors.red.A400
      }
    }
  })
}
