import {
  ColorLensTwoTone,
  ExpandLess,
  ExpandMore,
  ShuffleRounded,
  TextIncreaseRounded,
  TextFieldsRounded,
  SpaceDashboardRounded
} from '@mui/icons-material'
import {
  Button,
  Collapse,
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText
} from '@mui/material'
import * as React from 'react'
import { useContext } from 'react'
import { AppContext, AppContextType } from '../contexts'
import { Themes } from './Themes'
import { getTheme } from '../engine/theme'

const list = {
  alignItems: 'center',
  position: 'relative',
  width: '100%'
}

export function Sidebar() {
  const { store, dispatch } = useContext(AppContext) as AppContextType
  const [isOpen, setOpen] = React.useState({
    isOpenMode: false,
    isOpenFont: false,
    isOpenTheme: false
  })
  const currentTheme = getTheme(store.themeIndex)
  const fnOnClickShuffle = () => {
    dispatch({
      type: 'SHUFFLE_CARDS',
      event: { key: 'sidebarActive', value: !store.sidebarActive }
    })
  }

  return (
    <Drawer
      anchor="right"
      open={store.sidebarActive}
      onClick={() => {
        dispatch({
          type: 'SHOW_SIDEBAR',
          event: { key: 'sidebarActive', value: !store.sidebarActive }
        })
      }}
    >
      <List>
        <ListItem disablePadding>
          <ListItemButton
            dense={true}
            onClick={fnOnClickShuffle}
            disabled={!store.tableState}
          >
            <ListItemIcon>
              <ShuffleRounded />
            </ListItemIcon>
            <ListItemText primary="Shuffle On" />
          </ListItemButton>
        </ListItem>
        <Divider />
        <ListItem disablePadding>
          <ListItemButton
            dense={true}
            onClick={(e) => {
              e.stopPropagation()
              setOpen({
                ...isOpen,
                isOpenMode: !isOpen.isOpenMode
              })
            }}
          >
            <ListItemIcon>
              <SpaceDashboardRounded />
            </ListItemIcon>
            <ListItemText primary="Cards Mode" />
            {isOpen.isOpenMode ? <ExpandLess /> : <ExpandMore />}
          </ListItemButton>
        </ListItem>
        <Divider />
        <Collapse in={isOpen.isOpenMode} timeout="auto" sx={list}>          
          <Button
            sx={{
              backgroundColor:
                store.cntFrom === 1
                  ? currentTheme.palette.background.default
                  : currentTheme.palette.background.paper
            }}
            onClick={() =>
              dispatch({
                type: 'CARDS_MODE',
                event: {
                  key: 'cntFrom',
                  value: 1
                }
              })
            }
          >
            1:4
          </Button>
          <Button
            sx={{
              backgroundColor:
                store.cntFrom === 2
                  ? currentTheme.palette.background.default
                  : currentTheme.palette.background.paper
            }}
            onClick={() =>
              dispatch({
                type: 'CARDS_MODE',
                event: {
                  key: 'cntFrom',
                  value: 2
                }
              })
            }
          >
            2:4
          </Button>
          <Button
            sx={{
              backgroundColor:
                store.cntFrom === 3
                  ? currentTheme.palette.background.default
                  : currentTheme.palette.background.paper
            }}
            onClick={() =>
              dispatch({
                type: 'CARDS_MODE',
                event: {
                  key: 'cntFrom',
                  value: 3
                }
              })
            }
          >
            3:4
          </Button>          
        </Collapse>
        <ListItem disablePadding>
          <ListItemButton
            dense={true}
            onClick={(e) => {
              e.stopPropagation()
              setOpen({
                ...isOpen,
                isOpenFont: !isOpen.isOpenFont
              })
            }}
          >
            <ListItemIcon>
              <TextFieldsRounded />
            </ListItemIcon>
            <ListItemText primary="Font Size" />
            {isOpen.isOpenFont ? <ExpandLess /> : <ExpandMore />}
          </ListItemButton>
        </ListItem>
        <Divider />
        <Collapse in={isOpen.isOpenFont} timeout="auto" sx={list}>
          <Button
            onClick={() =>
              dispatch({
                type: 'FONT_SIZE',
                event: {
                  key: 'fontSize',
                  value: 'small'
                }
              })
            }
          >
            <TextIncreaseRounded fontSize="small" />
          </Button>
          <Button
            onClick={() =>
              dispatch({
                type: 'FONT_SIZE',
                event: {
                  key: 'fontSize',
                  value: 'medium'
                }
              })
            }
          >
            <TextIncreaseRounded fontSize="medium" />
          </Button>
          <Button
            onClick={() =>
              dispatch({
                type: 'FONT_SIZE',
                event: {
                  key: 'fontSize',
                  value: 'large'
                }
              })
            }
          >
            <TextIncreaseRounded fontSize="large" />
          </Button>
        </Collapse>
        <Divider />
        <ListItem disablePadding>
          <ListItemButton
            dense={true}
            onClick={(e) => {
              e.stopPropagation()
              setOpen({
                ...isOpen,
                isOpenTheme: !isOpen.isOpenTheme
              })
            }}
          >
            <ListItemIcon>
              <ColorLensTwoTone />
            </ListItemIcon>
            <ListItemText primary="Themes" />
            {isOpen.isOpenTheme ? <ExpandLess /> : <ExpandMore />}
          </ListItemButton>
        </ListItem>
        <Collapse in={isOpen.isOpenTheme} timeout="auto" sx={list}>
          <Themes />
        </Collapse>
      </List>
    </Drawer>
  )
}
