import { ThemeProvider } from '@mui/material'
import CssBaseline from '@mui/material/CssBaseline'
import * as React from 'react'
import { HomePage } from './HomePage'
import { ProfilePage } from './ProfilePage'
import { TablePage } from './TablePage'
import { Footer, Header, Sidebar } from './components'
import { AppContext, initState, initialState, reducer } from './contexts'
import { getTheme } from './engine/theme'
import { AndroidFullScreen } from '@awesome-cordova-plugins/android-full-screen'
import { Capacitor } from '@capacitor/core'
import { CategoryPage } from './CategoryPage'

// import { User, getAuth, onAuthStateChanged } from 'firebase/auth'

export function MainLayout() {
  const [store, dispatch] = React.useReducer(reducer, initialState, initState)

  if (Capacitor.isNativePlatform())
    AndroidFullScreen.isImmersiveModeSupported()
      .then(() => AndroidFullScreen.immersiveMode())
      .catch(console.warn)

  React.useEffect(() => {
    const _temp = localStorage.getItem('boomzaState')
    if (_temp !== null)
      dispatch({
        type: 'INIT',
        event: {
          key: 'all',
          value: JSON.parse(_temp)
        }
      })
  }, [])

  React.useEffect(() => {
    localStorage.setItem('boomzaState', JSON.stringify(store))
  }, [store])

  // const auth = getAuth()
  // onAuthStateChanged(auth, (user) => {
  //   if (user) {
  //     const uid = user.uid
  //     dispatch({
  //       type: 'SIGNED_IN',
  //       value: user,
  //       index: 0
  //     })
  //   } else {
  //     dispatch({
  //       type: 'SIGNED_OUT',
  //       value: null,
  //       index: 0
  //     })
  //   }
  // })

  return (
    <ThemeProvider theme={getTheme(store.themeIndex)}>
      <AppContext.Provider value={{ store, dispatch }}>
        <CssBaseline enableColorScheme />
        <Header />
        <Sidebar />
        {store.pageIndex === 0 && <HomePage />}
        {store.pageIndex === 1 && <TablePage />}
        {store.pageIndex === 2 && <ProfilePage />}
        {store.pageIndex === 3 && <CategoryPage />}        
        <Footer />
      </AppContext.Provider>
    </ThemeProvider>
  )
}
