import { Colorize } from '@mui/icons-material'
import { Button, Color, List, ListItem } from '@mui/material/'
import * as React from 'react'
import { AppContext, AppContextType } from '../contexts'
import { availableColors } from '../engine/theme'

const allColorsList = () => {
  const colorList: { [key: string]: object } = {}
  availableColors.forEach((color: Color, index: number) => {
    colorList[index] = {
      backgroundColor: color[900],
      color: color[50],
      borderRadius: '3em',
      height: '4.5em',
      width: '4.5em'
    }
  })
  return colorList
}

const sxStyles:  { [key: string]: object } = {
  ...allColorsList(),
  button: {
    margin: '1em'
  },
  extendedIcon: {
    marginRight: '1em'
  },
  list: {
    position: 'relative'
  },
  nested: {
    justifyContent: 'center',
    paddingLeft: 'auto'
  }
}

export function Themes() {
  const { dispatch } = React.useContext(AppContext) as AppContextType
  const onThemeChange = (index: number) => {
    dispatch({
      type: 'THEME_CHANGE',
      event: { key: 'themeIndex', value: index }
    })
  }

  return (
    <List component="div" disablePadding sx={sxStyles.list}>
      {availableColors.map((_color: Color, index: number) => (
        <ListItem key={index.toString()} sx={sxStyles.nested}>
          <Button
            onClick={() => onThemeChange(index)}
            sx={sxStyles[index]}
          >
            <Colorize />
          </Button>
        </ListItem>
      ))}
    </List>
  )
}
