import React from 'react'
import { IDeck, IDeckItem, IDeckList } from '../interfaces'
import { dealCards, mismatchCards, replaceCards, shuffleCards } from '../engine'

export interface IActionType {
  type: string
  event: {
    key: string
    value: unknown
  }
}

export interface IAppStateType {
  category: string
  deckList?: IDeckList | null
  currentDeckId?: string | null
  cntFrom: number
  cntTo: number
  themeIndex: number
  pageIndex: number
  sidebarActive: boolean
  expandedPanel: string
  user: object | null
  tableState: ITableStateType
  fontSize: 'small' | 'medium' | 'large'
}

export const initialState: IAppStateType = {
  deckList: null,
  category: '',
  cntFrom: 2,
  cntTo: 4,
  currentDeckId: null,
  themeIndex: 1,
  pageIndex: 0,
  sidebarActive: false,
  expandedPanel: 'first',
  user: null,
  tableState: {
    visible: { from: [], to: [] },
    list: []
  },
  fontSize: 'medium'
}

export const initState = () => initialState

export type AppContextType = {
  store: IAppStateType
  dispatch: (state: IActionType) => void
}

export const AppContext = React.createContext<AppContextType | null>(null)

export function reducer(
  state: IAppStateType,
  action: IActionType
): IAppStateType {
  switch (action.type) {
    case 'INIT':
      return {
        ...state,
        ...(action.event.value as object)
      }
    case 'LOAD_DECK':
      return {
        ...state,
        pageIndex: 1,
        tableState: shuffleCards(
          action.event.value as IDeck,
          state.cntFrom,
          state.cntTo
        )
      }
    case 'LOAD_CATEGORY':
      return {
        ...state,
        [action.event.key]: action.event.value,
        pageIndex: 3
      }
    case 'SHUFFLE_CARDS':
      return {
        ...state,
        [action.event.key]: action.event.value,
        pageIndex: 1,
        tableState: shuffleCards(
          state.tableState.deck,
          state.cntFrom,
          state.cntTo
        )
      }
    case 'CARDS_MODE':
      return {
        ...state,
        [action.event.key]: action.event.value,
        pageIndex: 1,
        tableState: shuffleCards(
          state.tableState.deck,
          action.event.value as number,
          state.cntTo
        )
      }
    case 'MATCHED':
      return {
        ...state,
        tableState: replaceCards(action.event.value as number, state.tableState)
      }
    case 'MISMATCHED':
      return {
        ...state,
        tableState: mismatchCards(
          action.event.value as number,
          state.tableState
        )
      }
    case 'RESET':
      return initialState
    default:
      return {
        ...state,
        [action.event.key]: action.event.value
      }
  }
}

type IVisibleType = {
  from: number[]
  to: number[]
}

export interface ITableStateType {
  deck?: IDeck
  visible: IVisibleType
  list: IDeckItem[]
}
