import SettingsSuggestSharpIcon from '@mui/icons-material/SettingsSuggestSharp'
import { AppBar, IconButton, Toolbar, Typography } from '@mui/material/'
import * as React from 'react'
import { AppContext, AppContextType } from '../contexts'

export function Header() {
  const { store, dispatch } = React.useContext(AppContext) as AppContextType
  // const Title = store.tableState.deck?.Title ?? 'Not Selected'
  const fnOnClick = () => {
    dispatch({
      type: 'SHOW_SIDEBAR',
      event: { key: 'sidebarActive', value: !store.sidebarActive }
    })
  }
  return (
    <AppBar position="static">
      <Toolbar>
        <img src="./img/mc.48x48.color.png" />
        <Typography variant="h5" width="100%">
          &nbsp;
          {store.pageIndex === 0 && <span>Memo Cards</span>}
          {store.pageIndex === 1 && <span>{store.tableState.deck?.Title}</span>}
          {store.pageIndex === 2 && <span>Profile</span>}
          {store.pageIndex === 3 && <span>{store.category}</span>}
        </Typography>
        <IconButton aria-label="Menu" onClick={fnOnClick}>
          <SettingsSuggestSharpIcon fontSize="large" />
        </IconButton>
      </Toolbar>
    </AppBar>
  )
}
