import {
  Avatar,
  List,
  ListItem,
  ListItemButton,
  ListItemText
} from '@mui/material'
import * as React from 'react'
import { AppContext, AppContextType } from './contexts'
import { getTheme } from './engine/theme'
import axios from 'axios'

export function CategoryPage() {
  const { store, dispatch } = React.useContext(AppContext) as AppContextType
  const currentTheme = getTheme(store.themeIndex)

  const fncOnClick = (deckId: string) => {
    if (!store.deckList) return
    const deck = store.deckList[store.category].Decks.find(
      (item) => item.DeckID === deckId
    )
    const url = store.deckList[store.category].Prefix + deck?.File
    axios
      .get(url)
      .then((response) =>
        dispatch({
          type: 'LOAD_DECK',
          event: {
            key: 'deckData',
            value: response.data
          }
        })
      )
      .catch((error) =>
        dispatch({
          type: 'ERROR',
          event: {
            key: 'error',
            value: error
          }
        })
      )
  }

  return (
    <List>
      {store.deckList &&
        store.deckList[store.category] &&
        store.deckList[store.category].Decks.map((item) => (
          <ListItem key={item.DeckID} disablePadding>
            <ListItemButton onClick={() => fncOnClick(item.DeckID)}>
              <Avatar
                sx={{
                  marginRight: 2,
                  fontSize: store.fontSize,
                  color: currentTheme.palette.background.paper,
                  backgroundColor: currentTheme.palette.text.primary
                }}
                aria-label="recipe"
              >
                {item.Title.toUpperCase()[0]}
              </Avatar>
              <ListItemText primary={item.Title} secondary={item.Description} />
            </ListItemButton>
          </ListItem>
        ))}
    </List>
  )
}
